*{
  padding:0;
  margin:0;
  box-sizing: border-box;
}

.App-header {
  max-width: 100vw;
  overflow: overlay;
  width:100%;
  padding: 20px 100px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header-left{
  cursor: pointer;
}

.header-right-top{
  display: flex;
  padding: 20px;
  margin-right: -50px;
  justify-content: flex-end;
  cursor: pointer;
}

.login{
  border-left: 1px solid black ;
  margin-left: 38px;
  /* margin-right: -10px; */
  padding-left: 77px;
  font-size: 18px;
}

.login img{
  margin-right: 10px;
  vertical-align: middle;
}

.login:hover{
  cursor: pointer;
  font-weight: 500;
}


.header-right-bottom{
  display: flex;
  width:100%;
  justify-content: space-around;
  align-items: center;
}

.navigate{
  margin:10px;
  font-size: 21px;
  color: #000000;
  opacity: 60%;
  padding-left: 37px;
  /* padding-right: 20px; */
}

.navigate:hover{
  cursor: pointer;
  font-weight: 500;
}

.banner{
  width:100%;
  display: flex;
  background-color: #ac0006;
}

.main{
  display: flex;
  flex-wrap: wrap;
}

.banner-box{
  width:50%;
  height:600px;
  position: relative;
  padding:40px 100px;
  margin-bottom: 100px;
  justify-content: center;
  align-items: center;
  /* overflow: hidden; */
}

.banner-text{
  height:100%;
  display: flex; 
  flex-direction: column;
  justify-content: space-evenly;
}


.banner-title{
  font-family: 'IvyPresto Display';
  font-style: normal;
  font-weight: 400;
  font-size: 62px;
}

.banner-desc{
  width: 100%;
  font-family: 'Niveau Grotesk';
  font-style: normal;
  font-weight: 400;
  line-height: 30px;
  font-size: 18px;
  /* margin: 26px 0; */
}

.portfolio-btn{
  cursor: pointer;
  width:fit-content;
  padding: 15px 50px;
  font-family: 'Niveau Grotesk';
  font-style: normal;
  font-weight: 700;
  font-size: 18.75px;
  background: #D7B344;
  border-radius: 4px;
  color: #000000;
}

.banner-right{
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  margin: 40px;
}

.img-wrap{
  /* height:100%; */
  display: flex;
  align-items: center;
  justify-content: center;
}

.banner-img1{
  width:50%;
  position: absolute;
  left: 30px;
  top: 60px;;
  z-index:3;
  border-radius: 16px;
}

.img-bgc{
  position: relative;
  top: 0;
  right: 0;
  width:100%;
  height:100%;
  max-width: 70%;
  background: #D8B243;
  border-radius: 16px;
  
}

.banner-img2{
  width:35%;
  max-width: 70%;
  position: absolute;
  bottom: 50px;
  right:100px;
  z-index:5;
  border-radius: 16px;
}

.banner-img3{
  width: 100%;
  height: 100%;
  position: relative;
  left:20px;
  top:-20px;
  border-radius: 16px;
  box-shadow: 5px 5px 35px rgba(0, 0, 0, .3);
  
}

.ellipse img{
  position: absolute;
  top: -234px;
  right: 0px;
}

.footer{
  display: flex;
}

.footer-item{
  cursor: pointer;
  width:33.33%;
  text-align: center;
  margin: 0 20px 80px 0;
}

.footer-item-title{
  font-size: 37px;
  Font-family: "IvyPresto Display";
  margin: 25px 0 20px 0;
}

.footer-item-desc{
  margin-right: 5px;
  color: #000000;
  opacity: 51%;
  font-size: 23px;
}


@media screen and (max-width:1040px){
  .App-header{
    display: inline-block;
    padding: 0 50px;
  }

  .banner-title{
    font-size: 40px;
  }

  .header-right-bottom{
    flex-direction: column;
    padding-top: 20px;
  }

  .login{
    border-left: 1px solid black ;
    margin-left: 38px;
    padding-left: 37px;
    font-size: 18px;
  }

  .navigate{
    padding-top: 10px;
  }

  .header-left{
    position: absolute;
    top: 20px;
  }
  .header-right{
    margin-right: 0px;
  }
  .header-right-top{
    margin-left: 0;
    margin-right: 0;
    margin-top: 20px;
  }
  .header-right-bottom{
    margin-top: 20px;
  }
  .banner-box{
    margin-top: 20px;
    padding: 30px;
    padding-bottom: 40px;
  }

  .portfolio-btn{
    margin: 20px auto 0;
  }

  .banner-img1{
    left:20px;
  }
  .banner-img2{
    right:20px;
    bottom: 20px;
  }
  .banner-desc{
    margin-top: 30px;
    width:100%;
  }
  .img-bgc{
    height:100%;
  }
  .banner-box{
    width:100vw;
    height:fit-content;
    margin-bottom: 50px;
  }

  .banner-title{
    padding-top: 0;
  }

  .ellipse img{
    display: none;
  }

  .footer-item{
    margin-right: 0;
  }

}


@media screen and (max-width:600px){

  .App-header{
    display: inline-block;
    padding: 0 20px;
  }

  .banner-title{
    font-size: 40px;
  }

  .header-right-bottom{
    flex-direction: column;
    padding-top: 20px;
  }

  .login{
    border-left: 1px solid black ;
    margin-left: 25px;
    padding-left: 30px;
    font-size: 18px;
  }

  .navigate{
    padding-top: 10px;
    padding-left: 0;
  }

  .header-left{
    position: absolute;
    top: 20px;
  }
  .header-right{
    margin-right: 0px;
  }
  .header-right-top{
    margin-left: 0;
    margin-right: 0;
  }
  .header-right-bottom{
    margin-top: 20px;
  }

  .banner-box{
    padding: 30px;
  }

  .portfolio-btn{
    margin: 0 auto;
  }

  .banner-img1{
    left:20px;
  }
  .banner-img2{
    right:20px;
    bottom: 20px;
  }
  .banner-desc{
    margin-top: 30px;
    width:100%;
  }
  .img-bgc{
    height:100%;
  }
  .banner-box{
    width:100vw;
    height:fit-content;
    margin-bottom: 50px;
  }

  .banner-title{
    padding-top: 0;
  }
  .footer-item{
    width:100vw;
    margin-right: 0;
  }

  .ellipse img{
    display: none;
  }

  .footer{
    flex-wrap: wrap;
  }
  .portfolio-btn{
    margin-top: 30px;
  }
}


